*, *::before, *::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}


body {
  margin: 0;
  font-family: 'Lato', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #3e5959;
}

code {
  font-family: 'Lato', sans-serif;
}

ul li {
  list-style: none;
  padding: 0;
  margin: 0;
}

li a {
  text-decoration: none;
}

.container {
    width: 90%;
    margin: auto;
}

.order-table {
  border: 1px solid black;
}

.order {
  display: flex;
  align-items: center;
}

.customer-name,
.customer-order {
  border: 1px solid black;
}


.divTable{
	display: table;
	width: 100%;
}
.divTableRow {
	display: table-row;
}
.divTableHeading {
	background-color: #EEE;
	display: table-header-group;
}
.divTableCell, .divTableHead {
	border: 1px solid #999999;
	display: table-cell;
  padding: 3px 10px;
}
.divTableHeading {
	background-color: #EEE;
	display: table-header-group;
	font-weight: bold;
}
.divTableFoot {
	background-color: #EEE;
	display: table-footer-group;
	font-weight: bold;
}
.divTableBody {
	display: table-row-group;
}

.wrapper {
  display: flex;
  flex-direction: column;
  padding: 2rem;
}

.pickup-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}


.date-where-time {
  padding: 1rem;
  display: flex;
}

.spacer {
  height: 20px;
}


.box {
  padding: 1rem;
}

.address {
  margin-right: 50px;
}

input {
  margin: 1rem;
  padding: 1rem;
  font-size: 1rem;
  border-top: none;
  border-right: none;
  border-left: none;
  border-bottom: 2px solid #eee;
}

.input-field {
  margin-top: 1rem;
  margin-left: 0;
  margin-right: 1rem;
  padding: 1rem;
  font-size: 1rem;
}


.submit-btn {
  width: 200px;
  background: #93BF00;
  border-radius: 30px;
  display: block;
  margin-top: 40px;
}


/********************************
Home Page
*********************************/

h2 {
  color: #93BF00;
  font-size: 1.3rem;
}

.main-container {
  width: 80%;
  margin: auto;
  /* position: absolute; */
}

.hero-bkg{
  width: 100%;
  height: auto;
  background: url(/static/media/recycle-hero.b9acc812.jpg) center no-repeat;
  background-size: cover;
  /* z-index: -1;
